import request from '@/utils/request'


// 
export function saveArticle(data) {
  return request({
    url: '/article/add',
    method: 'post',
    data
  })
}

// 
export function deleteArticle(params) {
  return request({
    url: '/article/delete',
    method: 'delete',
    params
  })
}

// 
export function updateArticle(data) {
  return request({
    url: '/article/update',
    method: 'put',
    data
  })
}

// 
export function fetchArticle(params) {
  return request({
    url: '/article/list',
    method: 'get',
    params
  })
}

// 
export function fetchArticleTwo(params) {
  return request({
    url: '/article/cate',
    method: 'get',
    params
  })
}

// 
export function fetchArticleItem(params) {
  return request({
    url: '/article/item',
    method: 'get',
    params
  })
}

// 
export function uploadEditorFile(data) {
  return request({
    url: '/upload/images',
    method: 'post',
    data
  })
}

